body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #0f1e3d;
  min-height: 15vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.divider {
  width: 2px;
  height: auto;
  display: inline-block;
}

.container {
  width: calc(400px + 5vmin);
  clear: both;
  align: center;
  margin: 0 auto;
  font-size: calc(15px + 1vmin);
}

.container input {
  width: 100%;
  font-size: calc(15px + 1vmin);
  height: calc(17px + 1vmin);
  clear: both;
}

.comicList {
  background-color: #404559;
  color: white;
  width: calc(400px + 8vw);
  clear: both;
  align: center;
  margin: 0 auto;
  border: 2px solid black;
  border-radius: 15px;
  padding-bottom: 25px;
}

button {
  border: 2px solid black;
  height: calc(40px + 1em);
  width: calc(75px + 2em);
  opacity: 0.9;
  transition: 0.5s;
  font-size: calc(1px + 2vmin);
  display: inline-block;
}

.navButton {
  background-color: #0f1e3d;
  color: white;
  border: 2px solid white;
  height: calc(25px + 1em);
  width: calc(85px + 1em);
  padding: 5px 10px;
  opacity: 0.9;
  transition: 0.5s;
  font-size: calc(2px + 2vmin);
  display: inline-block;
}

button:hover {
  background-color: #0f1e3d;
  color: white;
  opacity: 1;
}

.navButton:hover {
  background-color: white;
  color: black;
  opacity: 1;
}

button:active {
  background-color: #282c34;
  color: white;
}

